import React, { useEffect } from 'react';
import { stopScroll, stayScroll } from '../../../utils';
import './index.less'
import './indexPc.less'

const RemoveDetailModal = (props) => {

  const { onNo, onYes } = props;

  useEffect(() => {
    stopScroll();
    return () => stayScroll();
  }, [])

  return <section  className='mask'>
    <div className='remove_detail_modal'>
      <img src={require('./icon_remove_detail.png')} />
      <p>Do you want to remove your details from our system?  </p>
      <div className='remove_detail_buttons'>
        <button onClick={onYes}>Yes</button>
        <button className='remove_detail_buttons_no' onClick={onNo}>No</button>
      </div>
    </div>
  </section>

}

export default RemoveDetailModal