import React, { useEffect, useMemo, useState } from 'react';
import { Icon, Loader, MyButton, ErrOrderList, ResultDishView, CardTypeImg, AdaptHeader } from '../../component';
import { action, connect, getValueFromQuery, getReturnPath, formatPhone, inRN, getStatusBarHeight } from '../../utils';

import './index.less';
import './indexPc.less';
import MenuBg from '../mMenu/menuBg';
import RemoveDetailModal from './removeDetailModal';
import RemoveExpirePage from './removeExpirePage';


const HistoryOrderDetailDirect = (props) => {

  const { orderDetail, loading, buttonloading, showErrorList, landing } = props;


  const m = getValueFromQuery('m', props.location.search);


  const [showRemoveDetail, setShowRemoveDetail] = useState(false)

  function onYes () {
    props.dispatch(action('historyOrder/removeCustomer', {
      callBack: () => {
        console.log('onYes  callBack')
        // props.history.goBack()
      }
    }))
  }

  function onNo () {
    setShowRemoveDetail(false)
  }

  const [loadOrderDetailCount, setLoadOrderDetailCount] = useState(0)

  useEffect(() => {

    let timmer;
    if (orderDetail.status !== 2 && process.env.NODE_ENV !== 'development') {
      timmer = setTimeout(() => {
        setLoadOrderDetailCount(loadOrderDetailCount + 1);
        props.dispatch(action('historyOrder/getOrderDetailByNotice', { m: m }))
      }, 10000)
    }
    return () => {
      timmer && clearTimeout(timmer)
    }
  }, [orderDetail]);

  useEffect(() => {
    if (m) {
      props.dispatch(action('historyOrder/getOrderDetailByNotice', { m: m }))

    }
  }, [])


  function renderStatus (data) {
    let top = {
      title: 'Awaiting confirmation', icon: 'iconicondengdaiqueren',
      des: null, color: '#F45D1D', background: '#F7E7E0', borderColor: '#FFA37B'
    };
    switch (data.status) {
      case 0:
        top = {
          title: 'Awaiting confirmation', icon: 'iconicondengdaiqueren',
          des: <span>Store will confirm your order soon. </span>,
          color: '#F45D1D',
          background: '#F7E7E0',
          borderColor: '#FFA37B'
        };
        break;
      case 1:
      case 2:
        let orderTypeText = "Estimated ready time ";
        if (data.type === 2)
          orderTypeText = "Estimated arrival time ";
        let deliveryText = orderTypeText + " as soon as possible!";
        if (data.readyTimeStr) {
          deliveryText = orderTypeText + data.readyTimeStr;

          top = {
            title: 'Order confirmed', icon: 'iconiconyiqueren',
            des: <span>{deliveryText}</span>, color: '#3DCBC9', background: '#D8F6F6', borderColor: '#3DCCCA'
          };
        } else {
          top = {
            title: 'Awaiting confirmation', icon: 'iconicondengdaiqueren',
            des: <span>Store will confirm your order soon. </span>, color: '#F45D1D', background: '#F7E7E0', borderColor: '#FFA37B'
          };
        }
        break;
      // case 2:
      //   top = {
      //     title: getCompletedText(data.type), icon: 'iconiconjinhangzhong',
      //     des: <span>Enjoy your food!</span>, color: '#FFA900', background: '#FFF3DB', borderColor: '#FFA37B'
      //   };
      //   break;
      case 3:
        let orderFailedText;
        if (data.failedDetail) {
          orderFailedText = data.failedDetail;
        } else {
          switch (data.failedType) {
            case 0:
              orderFailedText = 'The payment did not go through.';
              break;
            case 1:
              orderFailedText = 'The store is currently offline, please try again later.';
              break;
            case 2:
              orderFailedText = 'Your order has been rejected. Call the store for more information. ';
              break;
            case 3:
              orderFailedText = 'Your order has been canceled. Call the store for more information.';
              break;
            case 4:
              orderFailedText = 'Your order has been rejected. Call the store for more information.';
              break;
            case 5:
              orderFailedText = 'Your order failed due to a system error, please try again later.';
              break;
            case 6:
              orderFailedText = 'Your order failed due to a system error, please try again later.';
              break;
            default:
              orderFailedText = 'Your order failed due to a system error, please try again later.';
              break;
          }
        }

        top = {
          title: 'Order failed', icon: 'iconiconshibai',
          des: <div style={{ display: "grid" }}>
            {orderFailedText}
          </div>,
          color: '#6E6E6E', background: '#E4E4E4', borderColor: '#BEBEBE'
        };
        break;
      default:
        break;
    }
    return <div
      className='history_order_detail_left_top_status'
      style={{ color: top.color, background: top.background, borderColor: top.borderColor }}>
      <Icon type={top.icon} />
      <div>
        <h5>{top.title}</h5>
        <div>{top.des}</div>
      </div>
    </div>
  }

  function renderDishList (data) {
    return data.dishList && <ResultDishView dishList={data.dishList} currency={data.currency} dishTypeStyle='theme' />
  }



  function renderEditDishList (data) {
    return data.edited && <ResultDishView dishList={data.edited} currency={data.currency} dishTypeStyle='edited' />
  }

  function renderAddDishList (data) {
    return data.added && <ResultDishView dishList={data.added} currency={data.currency} dishTypeStyle='added' />
  }

  function renderRemoveDishList (data) {
    return data.removed && <ResultDishView dishList={data.removed} currency={data.currency} dishTypeStyle='removed' />
  }

  function orderAgain () {
    if (orderDetail?.shopId) {

      props.dispatch(action('historyOrder/checkOrderAgain', {
        uuid: orderDetail.uuid, shopId: orderDetail.shopId, callBack: ((isValid, list) => {
          if (isValid) {
            props.history.push(`shopDetail?rId=${orderDetail.shopId}`)
            // 将 orderList 的数据放入购物车
            list?.filter(item => item.isValid).map(item => {
              props.dispatch(action('cart/getDishDetailFromOrder', {
                rId: item.shopId,
                dishId: item.id,
                typeId: item.typeId,
                categoryId: item.categoryId,
                menuId: item.menuId,
                dish: item,
              }));
            })
          }
        })
      }))


    }
  }

  function renderShop () {
    return <section className='history_order_detail_left_item_container history_order_detail_shop' style={{ marginTop: inRN() ? '.8rem' : 0 }}>
      <img src={orderDetail?.shopLogo} className='history_order_detail_logo' />
      <p className='history_order_detail_title_name'>{orderDetail.shopName}</p>
      <div className='history_order_detail_title_address'>
        <Icon type={'iconicon_location_b'} />
        {orderDetail.shopAddr}
      </div>

      {orderDetail.status !== 2 && orderDetail.status !== 3 && <div className='history_order_detail_left_top_deractor_con'>
        <div className='history_order_detail_left_top_deractor' />
      </div>}

      {renderStatus(orderDetail)}

      {renderOrderAction()}
    </section>
  }

  function renderOrderAction () {

    return <section className='history_order_detail_order_action'>
      <p className='remove_detail' onClick={() => setShowRemoveDetail(true)}>Not your order? Remove your details here</p>
      <div className='history_order_detail_left_header_info_bottom'>
        <MyButton loading={buttonloading} onClick={orderAgain}>
          <Icon type='iconicon_cart' />
          Order it again
        </MyButton>
      </div>
      <p className='review_order' onClick={() =>
        props.history.push(`/historyOrderDetail?uuid=${orderDetail.uuid}`)
      }>Review your order</p>
      <p className='order_action_others'>Help others find nice food.</p>
    </section>
  }

  function renderDownload () {
    const appStoreUrl = landing?.appStoreUrl;
    const googlePlayUrl = landing?.googlePlayUrl;
    if (inRN()) {
      return <AdaptHeader />
    }
    if (!appStoreUrl && !googlePlayUrl) {
      return null
    }
    return <section className='down_load_c'>
      <MenuBg className='down_load_bg' />
      <div className='down_load_c_wrapper'>
        <div className='down_load_title'>
          <h1>Order Takeaway Online</h1>
          <Icon type={'iconlogo'} />
        </div>
        <p>Download the EasyDish App.</p>
        <div className='down_load_c_left_buttom'>
          {appStoreUrl && <a href={appStoreUrl} target="_blank">
            <img src={require('../../assets/AppStore.png')} />
          </a>}
          {googlePlayUrl && <a href={googlePlayUrl} target="_blank">
            <img src={require('../../assets/google_play.png')} />
          </a>}
        </div>
      </div>


    </section>
  }

  const isExpired = false

  return isExpired ? <RemoveExpirePage /> : <section className='history_order_detail_direct_page' >
    {loading ? <Loader loading={loading} /> :
      <section className='history_order_detail_container'>
        <div className='history_order_detail_left'>
          {renderDownload()}
          {renderShop()}
          <div className='history_order_detail_left_item_container'>
            <div className='history_order_detail_left_item_title'>
              <h5>Order info</h5>
              <p>Placed Online</p>
            </div>
            <div className='history_order_detail_left_item_desc'>
              <span># {orderDetail.traceView}</span>
              {
                orderDetail.dayNum && <p>({orderDetail.dayNum})</p>
              }
            </div>
            <div className='history_order_detail_left_item_date'>
              <span>{orderDetail.createTime}</span>
              {
                [{ name: 'Delivery', icon: 'icona-11', type: 2 },
                { name: 'Collection', icon: 'icona-22', type: 3 }].map((item, index) => {
                  if (item.type === orderDetail.type)
                    return <div
                      key={index}
                      className={'history_order_detail_left_item_ordertype'}>

                      <Icon type={item.icon} />
                      <span
                        key={index}
                      >
                        {item.name}
                      </span>
                    </div>
                })
              }
            </div>
          </div>

          <div className='history_order_detail_left_item_container'>
            <div className='history_order_detail_left_item_title'>
              <h5>Customer details</h5>
            </div>
            <div className='history_order_detail_left_item_desc history_order_detail_left_item_customer'>
              {orderDetail.address && <span className='history_order_detail_left_item_desc_addr'>{orderDetail.address}</span>}
              <span className='history_order_detail_left_item_desc_breakall'>
                <span className='history_order_detail_left_item_desc_breakall_first'>{orderDetail.userName}</span>|<span>{orderDetail.userEmail}</span>
              </span>
              <span className='history_order_detail_left_item_desc_breakall'>
                <span className='history_order_detail_left_item_desc_breakall_first' >{orderDetail.userPhone}</span>
              </span>
            </div>
          </div>

          <div className='history_order_detail_left_item_container'>
            <div className='history_order_detail_left_item_title'>
              <h5>
                {(!orderDetail.paidFee || (orderDetail.due - orderDetail.paidFee) > 0) && orderDetail.payStatus !== 1 ? "Due" :
                  `Paid${orderDetail.isRefund ? '(Refunded)' : ''}`}
                <span>
                  {!orderDetail.paidFee || (orderDetail.due - orderDetail.paidFee) > 0
                    ? orderDetail.currency + (orderDetail.due - orderDetail.paidFee).toFixed(2) : ""}
                </span>
              </h5>
            </div>
            {
              orderDetail.paymentList ?
                orderDetail.paymentList.map((paidItem, index) => {
                  return <div className="paid_list" key={index}>
                    <div className="order_info_number order_info_cash_box">
                      <div className='order_info_cash_box_left'>
                        <span className="order_paid_type">{paidItem.type.toLowerCase() === "online" ? "Online Payment" : paidItem.type}</span>
                        {
                          paidItem.cardType && <CardTypeImg cardType={paidItem.cardType} />
                        }
                        {
                          paidItem.cardNum && <span className="order_paid_card_number">{paidItem.cardNum}</span>
                        }
                      </div>
                      <div>
                        {paidItem.amount && orderDetail.currency + paidItem.amount?.toFixed(2)}
                      </div>
                    </div>
                    <div className="order_info_number_No">
                      <div>{paidItem.createTime}</div>
                      {/* {
                        paidItem.status && <div className="order_paid_status">{paidItem.status}</div>
                      } */}
                    </div>
                  </div>
                })
                : null
            }
          </div>


        </div>
        <aside>
          <div className='history_order_detail_left_dec_container'>
            <h5 className='history_order_detail_left_dec_header'>Your order</h5>
            {
              orderDetail.note &&
              <div className='history_order_detail_left_dec_note'>{orderDetail.note}</div>
            }
            {renderDishList(orderDetail)}
            {renderEditDishList(orderDetail)}
            {renderAddDishList(orderDetail)}
            {renderRemoveDishList(orderDetail)}
            <div className='history_order_detail_left_price_container'>
              <div className='history_order_detail_left_price_item'>
                <span>Subtotal:</span>
                <p>{orderDetail.currency}{orderDetail.price?.toFixed(2)}</p>
              </div>
              {
                orderDetail.deliveryFee != 0 &&
                <div className='history_order_detail_left_price_item'>
                  <span>Delivery Charge: </span>
                  <p>{orderDetail.currency}{orderDetail.deliveryFee?.toFixed(2)}</p>
                </div>
              }
              {
                orderDetail.serviceFee != 0 &&
                <div className='history_order_detail_left_price_item'>
                  <span>Online Service Fee: </span>
                  <p>{orderDetail.currency}{orderDetail.serviceFee?.toFixed(2)}</p>
                </div>
              }
              {
                orderDetail.amountDiscount != 0 &&
                <div className='history_order_detail_left_price_item'>
                  <span>Coupon Discount: </span>
                  <p>-{orderDetail.currency}{orderDetail.amountDiscount?.toFixed(2)}</p>
                </div>
              }
              {
                orderDetail.orderDiscount != 0 &&
                <div className='history_order_detail_left_price_item'>
                  <span>Discount: </span>
                  <p>-{orderDetail.currency}{orderDetail.orderDiscount?.toFixed(2)}</p>
                </div>
              }


              <div className='history_order_detail_left_price_item'>
                <h3>Total:  </h3>
                <p className='history_order_detail_totoal_price'>{orderDetail.currency}{orderDetail.due?.toFixed(2)}</p>
              </div>
            </div>

          </div>
        </aside>

      </section>
    }
    {showErrorList && <ErrOrderList />}
    {showRemoveDetail && <RemoveDetailModal onYes={onYes} onNo={onNo} />}
  </section>

}


export default connect(({
  historyOrder: { orderDetail, loading, buttonloading, showErrorList },
  webConfigModal: { landing }
}) => ({
  orderDetail, loading, buttonloading, showErrorList,
  landing
}))(HistoryOrderDetailDirect);